.default-background {
  background-color: #e7e5e4;
}
.frontpage {
  width: 100%;
  height: 100%;
  position: absolute;
}
.frontheader {
  color: white;
  font-size: 50px;
  font-family: PlayfairDisplay-Bold;
  text-align: center;
}
@font-face {
  font-family: PlayfairDisplay-Bold;
  src: url(../font/PlayfairDisplay-Bold.otf);
}
.fronttext {
  color: white;
  font-size: 15px;
  font-family: PlayfairDisplay-Bold;
  text-align: center;
}
.svgcircle {
  height: 105px;
  width: 105px;
}
.explorecircle {
  height: 105px;
  width: 105px;
  cx: 50;
  cy: 50;
  r: 49;
  fill: transparent;
  stroke: white;
  stroke-opacity: 0.5;
  stroke-width: 3px;
  stroke-dasharray: 500;
}
@keyframes showcircle {
  from {
    stroke-dashoffset: 500;
  }
  to {
    stroke-dashoffset: 0;
  }
}
.explore:hover .explorecircle {
  animation: showcircle 1s;
  stroke-opacity: 1;
}
.frontbanner {
  top: 150px;
  position: absolute;
  animation: fadeIn 3s;
  text-align: center;
  width: 100%;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes transition {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}
.exploretext {
  stroke: white;
  stroke-opacity: 1;
  fill-opacity: 1;
  fill: white;
  font-size: 15px;
  font-family: PlayfairDisplay-Bold;
}
