.menucontainer {
  height: 80px;
  width: 80px;
  perspective: 1000;
  position: relative;
  float: left;
}
.menu {
  transform-style: preserve-3d;
  transition: transform 1s;
}
.menucontainer:hover .menu {
  transform: rotateY(180deg);
}
.menufront,
.menuback {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  height: 80px;
  width: 80px;
}
.menufront {
  background: transparent url('../img/menu_white.svg') center center no-repeat;
  background-size: 60%;
  z-index: 2;
}
.menuback {
  background: white url('../img/menu_grey.svg') center center no-repeat;
  background-size: 60%;
  transform: rotateY(-180deg);
}
.menubar {
  position: sticky;
}
